import { Box, Link, Typography } from "@mui/material";
import { ProjectDetailSubPageModel } from "../../../../../Models/Models";

interface PropTypes {
  buttonRef: React.MutableRefObject<null>;
  data: ProjectDetailSubPageModel[];
}
const ProjectDetailSubPage = (props: PropTypes) => {
  const { buttonRef, data } = props;
  return (
    <Box
      ref={buttonRef}
      display="grid"
      gridTemplateColumns="auto auto"
      sx={{
        width: "calc(100vw - 700px)",
        padding: "80px",
        paddingBottom: "160px",
        gap: "60px",
      }}
    >
      {data?.slice(0, data.length - 1).map((details) => (
        <Box key={details?.id}>
          <Typography variant="h4" color="#9CA6AD">
            {details?.type}
          </Typography>
          <Typography
            variant="body1"
            sx={{ fontSize: "24px", lineHeight: "29px" }}
          >
            {details?.description}
          </Typography>
        </Box>
      ))}
      {data && data.length > 0 && (
        <Box key={data[data.length - 1]?.id}>
          <Typography variant="h4" color="#9CA6AD">
            {data[data.length - 1]?.type}
          </Typography>
          <Link href={data[data.length - 1]?.description} variant="body1" sx={{ color: "#353535", textDecorationColor: "#353535", fontSize: "20px" }}>
            {data[data.length - 1]?.description}
          </Link>
        </Box>
      )}
    </Box>
  );
};

export default ProjectDetailSubPage;
